<template>
  <div class="publish-container" v-loading="uploadLoading">
    <div class="rules-area box-class">
      <span class="title-class">服务选择</span>
      <el-form label-position="left" :model="serviceData" label-width="150px" size="small">
        <el-form-item label="服务类别：">
          {{baseData.classification}}
        </el-form-item>
        <el-form-item label="服务名称：">
          {{baseData.name}}
        </el-form-item>
      </el-form>
    </div>
    <div class="rules-area box-class">
      <span class="title-class">发布策略</span>
      <el-form label-position="left" :model="imeiData" label-width="150px" size="small" class="imei-form">
      <el-form-item label="客户端最低版本号：">
        {{imeiData.clientVersion}}
      </el-form-item>
        <el-form-item label="发布方式：">
          <span v-if="imeiData.type===0">全量发布</span>
          <span v-if="imeiData.type===1">灰度发布</span>
        </el-form-item>
        <el-form-item label="控制方式：" v-if="imeiData.type===1" >
          <span v-if="greyType0">IMEI控制 </span>
          <span v-if="greyType1"> 用户标签</span>
        </el-form-item>
        <el-form-item label="定向灰度：" v-if="imeiData.type===1" >
          <span v-if="imeiData.imeiGreyType===0">指定IMEI </span>
          <span v-if="imeiData.imeiGreyType===1"> 屏蔽IMEI</span>
        </el-form-item>
        <el-form-item label="上传IMEI方式：" v-if="imeiData.type===1" >
          <template v-if="grayscaleDataTypes[0]===1">
            <div>手动填写(≤10个)</div>
            <el-tag v-for="(imei, index) in imeiList" :key="index" type="info" style="margin-right: 5px;">
              {{imei}}
            </el-tag>
          </template>
          <template v-if="grayscaleDataTypes[0]===2">
            <div>上传文件(txt格式，每行一个IMEI码，请不要添加其他字符)</div>
            <div v-if="excelFileName">{{excelFileName}}</div>
            <a class='download' :href='downloadFileUrl' download="下载" title="下载" v-if="!excelFileName&&downloadFileName" target="_blank">{{downloadFileName}}</a>
          </template>
        </el-form-item>
        <el-form-item label="尾数灰度：" v-if="imeiData.type===1&&imeiData.greyTypeList.length>0&&(imeiData.greyTypeList[0]===0||imeiData.greyTypeList[1]===0)" >
          <el-checkbox v-model="isMantissaList1" disabled>使用1个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList1" v-if="isMantissaList1" disabled>
            <el-checkbox v-for="n in 10" :label="getForNumber(n,1)" :key="getForNumber(n,1)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,1)}}</el-checkbox>
          </el-checkbox-group>
          <el-checkbox v-model="isMantissaList2" disabled>使用2个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList2" v-if="isMantissaList2" disabled>
            <el-checkbox v-for="n in 100" :label="getForNumber(n,2)" :key="getForNumber(n,2)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,2)}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>

        <el-form-item label="用户标签：" v-show="imeiData.type===1&&imeiData.greyTypeList.length>0&&(imeiData.greyTypeList[0]===1||imeiData.greyTypeList[1]===1)">
            <treeselect 
            :load-options="loadOptions" :max-height="500" placeholder="请选择或者输入关键字搜索" v-model="imeiData.tags" 
            :multiple="true" :options="tagTreeData" :clearable="true" :searchable="true" :disabled="true">
            </treeselect>
        </el-form-item>
      </el-form>
    </div>

        <!-- 审核进度 -->
    <div class="notice-area box-class">
      <span class="title-class">审核进度</span>
      <div class="check-div" v-if="approvalData.approvalTestStatus===0"><i class="el-icon-remove" style="color: #909399"></i>测试人员未审核</div>
      <div class="check-div" v-if="approvalData.approvalTestStatus===1"><i class="el-icon-success" style = "color: #409EFF"></i>测试人员审核：测试工程师{{approvalData.approvalTestUserName}}审核通过</div>
      <div class="check-div" v-if="approvalData.approvalTestStatus===2"><i class="el-icon-error" style="color: #f56c6c;"></i>测试人员审核：测试工程师{{approvalData.approvalTestUserName}}审核不通过原因为：{{approvalData.approvalTestReason}}</div>

      <div class="check-div" v-if="approvalData.approvalOperationStatus===0"><i class="el-icon-remove" style="color: #909399"></i>运营人员未审核</div>
      <div class="check-div" v-if="approvalData.approvalOperationStatus===1"><i class="el-icon-success" style = "color: #409EFF"></i>运营人员审核：运营经理{{approvalData.approvalOperationUserName}}审核通过</div>
      <div class="check-div" v-if="approvalData.approvalOperationStatus===2"><i class="el-icon-error" style="color: #f56c6c;"></i>运营人员审核：运营经理{{approvalData.approvalOperationUserName}}审核不通过原因为：{{approvalData.approvalOperationReason}}</div>
    </div>

    <div class="box-class" v-if="isDetailpage&&bohuiReason">
      <div class="reason-area">
        <div style="font-size: 19px;font-weight: bold;">驳回原因</div>
        <div>{{bohuiReason}}</div>
      </div>
    </div>
    <div class="buttons-area box-class" v-if="isCheckpage">
      <el-button @click="goHome" type="primary" round class="button2-class">返回</el-button>
      <el-button @click="passButton(true)" type="primary" round class="button2-class" :disabled="isExam">通过</el-button>
      <el-button @click="passButton(false)" type="primary" round class="button-class" :disabled="isExam">不通过</el-button>
    </div>
    <div class="buttons-area box-class" v-if="isEditPage&&stepNumber!==3">
      <el-button v-if="stepNumber===1" @click="cancelEditButton()" type="primary" round class="button-class">取消编辑</el-button>
      <el-button v-if="stepNumber===1" @click="saveButton()" type="primary" round class="button-class">提交审核</el-button>
    </div>
    <el-dialog title="审核说明" :visible.sync="examDialogVisible" width="600px" :before-close="examDialogClose" center>
      <el-form :model="baseData" ref="examData" label-width="100px">
        <el-form-item label=" 审核说明" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" type="textarea" :autosize="{ minRows: 2, maxRows: 3}" placeholder="请填写审核说明" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="examDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="examDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import global from "../service/global";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

export default {
  name: "publish-component",
  components: { Treeselect },
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    isAdd: {
      type: Boolean,
      default: false
    },
    isDetailpage: {
      type: Boolean,
      default: false
    },
    typeData: {},
    baseData: {}
  },
  data() {
    var weightValidate = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入初始权值"));
      } else if (/^\d+$/.test(value)) {
        let newValue = Number(value);
        if (newValue >= 0 && newValue <= 50) {
          callback();
        } else {
          callback(new Error("请输入整数0-50"));
        }
      } else {
        callback(new Error("请输入整数0-50"));
      }
    };
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error("不能输入全空格"));
      } else {
        callback();
      }
    };
    return {
      approvalData:{},
      tagTreeData: [],
      serviceData: {
        classification: '',
        name: ''
      },
      typeList: [
        'tips','卡片'
      ],
      defaultProps: {
        value: 'id',
        children: "children",
        label: "label"
      },
      grayscaleDataTypes: [],
      excelFileName: "",
      excelFile: "",
      uploadLoading: false,
      greyType: 1,
      downloadFileName: '',
      downloadFileUrl: '',
      isMantissaList1: false,
      isMantissaList2: false,
      imeiList: [],
      imeiValue: "",
      imeiData: {
        grayscaleDataType: '',
        type: '',
        greyTypeList: []
      },
      mantissaList1: [],
      mantissaList2: [],
      startTimeDisabled: false,
      testCardUrl: "",
      isExam: false,
      xiajiaDialogVisible: false,
      jinyongDialogVisible: false,
      isEditPage: true,
      examDialogVisible: false,
      testImgSrc: "",
      testDialogVisible: false,
      bohuiReason: "",
      cardPreview: "",
      expandPreview: "",
      holidayList: global.holidayList,
      //peopleLabels: [{ key: 1, value: "年轻人" }, { key: 2, value: "老年人" }],
      stepNumber: 1,
      timeData: {
        type: 0,
        startTime: "",
        endTime: "",
        holiday: ""
      },
      weightData: {
        order: "",
        attenuationDuration: ""
      },
      serviceRule: {
        classification: [
          { required: true, message: "请选择服务类别", trigger: "blur" }
        ],
        strategyId: [
          { required: true, message: "请选择服务名称", trigger: "blur" }
        ]
      },
      imeiRule: {

      },
      applyRules: [
        { required: true, message: "内容不能为空", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  
  methods: {
    appendId(arr, id) {
      for (let i = 0; i < arr.length; i++) {
        if (id) {
          arr[i]["id"] = id + "_" + arr[i].key;
        } else {
          arr[i]["id"] = "" + arr[i].key;
        }
        if (arr[i]["children"]) {
          this.appendId(arr[i]["children"], arr[i]["id"]);
        }
      }
      return arr;
    },
    appendKeyId(arr, keyId) {
      for (let i = 0; i < arr.length; i++) {
        if (keyId) {
          arr[i]["keyId"] = keyId + "-" + i;
        } else {
          arr[i]["keyId"] = "" + i;
        }
        if (arr[i]["children"]) {
          appendId(arr[i]["children"], arr[i]["keyId"]);
        }
      }
      return arr;
    },
    childrenToNull(children) {
      let arr = [];
      if (children) {
        for (let i = 0; i < children.length; i++) {
          arr[i] = {
            id: children[i]["id"],
            label: children[i]["label"],
            hasScore: children[i]["hasScore"]
          };
          if (children[i]["children"] && children[i]["children"].length > 0) {
            arr[i]["children"] = null;
          }
        }
      } else {
        arr = undefined;
      }

      return arr;
    },
    findChildById(tagData, keyId) {
      var arrs = keyId.split("_");

      var item = JSON.parse(JSON.stringify(tagData));
      let i = 0;
      for (; i < arrs.length; i++) {
        item = item[parseInt(arrs[i])]["children"];
      }

      return this.childrenToNull(item);
    },
    loadOptions({ action, parentNode, callback }) {
      if (action === "LOAD_CHILDREN_OPTIONS") {
        parentNode.children = this.findChildById(this.tagTreeData, parentNode.id);
        callback();
      }
    },
    //尾数
    getForNumber(number, type) {
      let newNumber = number - 1;
      if (type === 1) {
        return newNumber + '';
      } else {
        if (newNumber < 10) {
          return '0' + newNumber;
        } else {
          return newNumber + '';
        }
      }
    },
    //审核操作
    passButton(type) {
      if (type) {
        let param = {
          strategyId: this.baseData.strategyId,
          status: 1,
        };
        this.$http
          .post("/scene/approval", param)
          .then(response => {
            if (response.data && response.data.code == "0") {
              this.$message({
                type: "success",
                message: "操作成功"
              });
              this.baseData.state = 6;
              this.isExam = true;
              this.$emit("publish-event", {
                type: "home-page"
              });
            } else {
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
            }
          })
          .catch(() => {
            this.$message.error("操作失败！");
          });
      } else {
        this.examDialogVisible = true;
      }
    },
    //审核弹窗
    examDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.examDialogVisible = false;
      } else {
        this.$refs["examData"].validate(valid => {
          if (valid) {
            let param = {
              strategyId: this.baseData.strategyId,
              status: 2,
              reason: this.baseData.reason
            };
            this.$http
              .post("/scene/approval", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: "操作成功"
                  });
                  this.baseData.state = 0;
                  this.isExam = true;
                  this.examDialogVisible = false;
                  this.$emit("publish-event", {
                    type: "home-page"
                  });
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("操作失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("操作失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //跳转数据页面
    goDataPage() {
      this.$emit("publish-event", {
        type: "data-page"
      });
    },
    //回到列表页面
    goHome() {
      this.$emit("publish-event", {
        type: "home-page"
      });
    },
    isEmpty(key) {
      if (typeof key === 'undefined' || key === '' || key === null) {
        return true;
      } else {
        return false;
      }
    },
    //获取数据xhr
    getDataXhr() {
      let param = {
        strategyId: this.baseData.strategyId
      };
      this.$http
        .post("/scene/getPublishDetail", param)
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let pubulishDTO = response.data.data;
            this.serviceData.classification = pubulishDTO.classification
            this.serviceData.strategyId = pubulishDTO.strategyId;
            this.imeiData.type = pubulishDTO.type;
            this.imeiData.imeiGreyType = pubulishDTO.imeiGreyType&&parseInt(pubulishDTO.imeiGreyType);
            console.log(this.imeiData.imeiGreyType)
            this.imeiData.clientVersion = pubulishDTO.clientVersion;
            this.imeiData.greyTypeList = pubulishDTO.greyType?pubulishDTO.greyType.split(',').map(Number):[];
            this.imeiData.imeiDefineTypeList = pubulishDTO.imeiDefineType?pubulishDTO.imeiDefineType.split(',').map(Number):[];
            this.downloadFileName = pubulishDTO.fileName;
            this.downloadFileUrl = pubulishDTO.fileUrl;
            // 手动填写
            if(pubulishDTO.imeiManual){
              this.grayscaleDataTypes=[1]
              this.imeiList = pubulishDTO.imeiManual.split(',').map(Number);
            }
            //控制方式
            let list = pubulishDTO.greyType?pubulishDTO.greyType.split(',').map(Number):[];
            this.greyType0 = list.length>0&&(list[0]===0||list[1]===0)?true:false;
            this.greyType1 = list.length>0&&(list[0]===1||list[1]===1)?true:false;
            // 上传文件
            if(pubulishDTO.fileUrl){
              this.grayscaleDataTypes=[2];
              this.downloadFileName = pubulishDTO.fileName;
              this.downloadFileUrl = pubulishDTO.fileUrl;
            }
            this.imeiData.tags = pubulishDTO.tags&&pubulishDTO.tags.split(',');

            if(pubulishDTO.imeiEndNums){
              let imeiEndList = pubulishDTO.imeiEndNums.split(',');
              imeiEndList.forEach(e => {
                if(e.length===1){
                  this.mantissaList1.push(e)
                }else if(e.length===2){
                  this.mantissaList2.push(e)
                }
              });
              this.isMantissaList2 = this.mantissaList2.length>0?true:false;
              this.isMantissaList1 = this.mantissaList1.length>0?true:false;
            }
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    getApprovalStatus() {
      let param = {
        strategyId: this.baseData.strategyId
      };
      this.$http
        .post("/scene/approvalStatus", param)
        .then(response => {
          if (
            response.data.code === 0
          ) {
            this.approvalData = response.data.data;
          }
          })
        .catch(() => {
          //console.log(error);
        });
    },
    getTagList() {
      this.$http
        .post("/scene/getTagTree", {})
        .then(response => {
          if (
            response.data.code === 0
          ) {
            let children = response.data.data.children;
            children = this.appendId(children);
            this.tagTreeData  = children;

          }
          })
        .catch(() => {
          //console.log(error);
        });
    }
  },
  //初始化请求页面数据
  mounted: function () {
    if (this.isCheckpage || this.isDetailpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.id !== 'undefined') {
      }
    this.getDataXhr();
    this.getTagList();
    this.getApprovalStatus();
  },
  created: function () {
  }
};
</script>
